import * as React from "react"
import { type CommonProps } from "../../types"
import { classNames } from "../../utils"
import { Spinner } from "./Spinner"
import { ButtonContentProps } from "./ButtonTypes"

export interface ButtonIconProps
  extends CommonProps,
    Pick<ButtonContentProps, "buttonVariant" | "buttonSize" | "isLoading"> {
  icon: ButtonContentProps["iconLeft"]
}
const ButtonIcon = ({ buttonVariant, buttonSize, isLoading = false, icon, className = "" }: ButtonIconProps) => {
  if (!icon && !isLoading) return null

  return (
    <span>
      {isLoading ? (
        <React.Fragment>
          <Spinner
            variant={["standard", "ghost"].includes(buttonVariant as string) ? "blue" : "plain"}
            size={buttonSize === "xl" ? "md" : "sm"}
            className={classNames([buttonSize !== "xl" ? "translate-y-0.5" : "translate-y-1", className])}
          />
        </React.Fragment>
      ) : (
        icon
      )}
    </span>
  )
}

const ButtonContent = React.forwardRef<HTMLSpanElement, ButtonContentProps>(
  (
    {
      children,
      textProps,
      buttonVariant,
      buttonSize = "md",
      isLoading = false,
      iconLeft = null,
      iconRight = null,
      className = "",
      ...props
    },
    ref
  ) => {
    return (
      <span
        className={classNames([
          "instant-noodles-ui-element instant-noodles-ui-button-content", // custom class name for any global customization
          "flex flex-row items-center space-x-2 justify-center",
          className,
        ])}
        ref={ref}
        {...props}
      >
        {/* only show loading icon on left side */}
        <ButtonIcon
          buttonVariant={buttonVariant}
          buttonSize={buttonSize}
          isLoading={isLoading}
          icon={iconLeft}
          className="mr-2"
        />
        <span {...textProps}>{children}</span>
        <ButtonIcon buttonVariant={buttonVariant} buttonSize={buttonSize} icon={iconRight} className="ml-2" />
      </span>
    )
  }
)

ButtonContent.displayName = "ButtonContent"

export default ButtonContent
