import * as React from "react"
import type { HTMLReport, ReportFilter, ReportStats, ExpandedTests } from "../../types"
import * as icons from "../Icons"
import { SuiteResults } from "../Suite"
import { ReportHeader } from "./ReportHeader"
import { ToggleReportButton } from "./ToggleReportButton"
import { useOnClickOutside } from "../../hooks/use-on-click-outside"

export interface IReportContent {
  stats?: ReportStats
  report?: HTMLReport
  isShowing?: boolean
  setIsShowing: (b: boolean) => void
  displayFilter: ReportFilter
  setDisplayFilter: (filter: ReportFilter) => void
  expandedTests: ExpandedTests
  setExpandedTests: (expandedTests: ExpandedTests) => void
  testsAreRunning?: boolean
}
export function ReportContent(props: IReportContent) {
  const reportWrapperRef = React.useRef<HTMLDivElement>(null)

  const handleOnClickOutside = () => {
    // TODO: only close report on outside click if configuration option is set to true
    props.setIsShowing(false)
  }

  useOnClickOutside(reportWrapperRef, handleOnClickOutside)

  if (!props.isShowing) {
    return (
      <ToggleReportButton
        numPassed={props.report?.stats?.passed ?? 0}
        numTotal={props.report?.stats?.total ?? 0}
        setIsShowing={props.setIsShowing}
      />
    )
  }

  return (
    <div className="htmlreport" ref={reportWrapperRef}>
      <div className="report-close-icon" role="button" onClick={() => props.setIsShowing(!props.isShowing)}>
        {icons.cross()}
      </div>
      <main>
        <ReportHeader
          stats={props.stats}
          displayFilter={props.displayFilter}
          setDisplayFilter={props.setDisplayFilter}
        />
        <br />
        <br />
        <SuiteResults
          displayFilter={props.displayFilter}
          report={props.report}
          expandedTests={props.expandedTests}
          setExpandedTests={props.setExpandedTests}
          testsAreRunning={props.testsAreRunning}
        />
      </main>
    </div>
  )
}
