import * as React from "react"
import type { TestSuiteMapping, ReportResults, AnyFunction } from "../types"
import * as microwave from "react-microwave"
import { useDebouncedCallback } from "./use-debounced-callback"

export interface UseExecuteTestSuitesContext {
  report: Partial<ReportResults>
  testsAreRunning: boolean
}

export interface UseExecuteTestSuitesParams {
  testSuites?: TestSuiteMapping
  RootComponent?: React.ElementType
  config?: microwave.MicrowaveConfig
}

let testsRun = 0

function registerCallbackOnViteHMREvent(cb: AnyFunction = () => {}, config: microwave.MicrowaveConfig = {}) {
  if (import.meta.hot) {
    if (config?.debug) console.log("[REACT-INSTANT-NOODLES]: hot", { data: import.meta.hot?.data })
    import.meta.hot.on("vite:beforeUpdate", (data: any) => {
      if (config?.debug) console.log("[REACT-INSTANT-NOODLES]:VITE HMR Data", { data })
      cb(data)
    })
  }
}

// TODO: Implement this correctly for webpack
function registerCallbackOnWebackHMREvent(cb: AnyFunction = () => {}, config: microwave.MicrowaveConfig = {}) {
  if (import.meta.hot) {
    if (config?.debug) console.log("[REACT-INSTANT-NOODLES]: hot", { data: import.meta.hot?.data })
    import.meta.hot.on("webpack:beforeUpdate", (data: any) => {
      if (config?.debug) console.log("[REACT-INSTANT-NOODLES]: WEBPACK HMR Data", { data })
      cb(data)
    })
  }
}

export function useExecuteCallbackOnHMR(cb: AnyFunction = () => {}, config: microwave.MicrowaveConfig = {}) {
  const [init, setInit] = React.useState(false)
  const listener = React.useRef<AnyFunction | null>(null)

  // eslint-disable-next-line
  React.useEffect(() => {
    if (!init) {
      if (config?.debug) console.log("[REACT-INSTANT-NOODLES]: HMR callback handler initialized")
      registerCallbackOnViteHMREvent(cb, config)
      registerCallbackOnWebackHMREvent(cb, config)
      listener.current = cb
      setInit(true)
    }
  })
}

export async function executedTestsResults({ testSuites, RootComponent }: Omit<UseExecuteTestSuitesParams, "runs">) {
  if (!RootComponent) {
    console.log(`[REACT-INSTANT-NOODLES]: No RootComponent provided. Skipping test execution.`)
    return null
  }

  for (const testSuiteName in testSuites) {
    const testSuite = testSuites[testSuiteName]
    const register = testSuite(RootComponent)
    if (!register) {
      console.log(
        `[REACT-INSTANT-NOODLES]: No suite object returned for ${testSuiteName}. Skipping tests for ${testSuiteName}.`
      )
      continue
    }
    // auto register test suites in case the user forgot to do it
    register.run()
  }

  const results = await microwave.exec()

  return results.toJson()
}

export function useExecuteTestSuites({
  testSuites,
  RootComponent,
  config,
}: UseExecuteTestSuitesParams): UseExecuteTestSuitesContext {
  const [testsAreRunning, setTestsAreRunning] = React.useState(false)
  const [reportResults, setReportResults] = React.useState<Partial<ReportResults>>({})
  const timeout = React.useRef<ReturnType<typeof setTimeout> | null>(null)

  const debouncedExecuteTests = useDebouncedCallback(() => {
    const runTests = async () => {
      setTestsAreRunning(true)
      const results = await executedTestsResults({ testSuites, RootComponent })
      setReportResults((s) => ({ ...s, ...results }))
      testsRun++
      if (config?.debug) console.log(`[REACT-INSTANT-NOODLES]: TEST RUNS ${testsRun}`)
      setTestsAreRunning(false)
    }

    runTests()
  }, 50)

  React.useEffect(() => {
    // TODO: Improve this debounced double test execution to force a report render
    setTimeout(() => {
      debouncedExecuteTests()
    }, 150)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  // // TODO: Find a better way to do this
  useExecuteCallbackOnHMR((data) => {
    if (config?.debug)
      console.log({ message: "[REACT-INSTANT-NOODLES]: Creating timeout to execute tests after HMR", data })
    // give the hot module reload time to update the module
    // before running the tests
    if (timeout.current) {
      clearTimeout(timeout.current)
    }
    timeout.current = setTimeout(() => {
      if (config?.debug) console.log("[REACT-INSTANT-NOODLES]: Running tests after HMR")
      debouncedExecuteTests()
    }, 100)
  }, config)

  return { report: reportResults, testsAreRunning }
}
