export function isString(value: any): value is string {
  return typeof value === "string"
}

export function isUndefined(value: any): value is undefined {
  return typeof value === "undefined"
}

export function isNull(value: any): value is null {
  return value === null
}

export function isNil(value: any): value is null | undefined {
  return isNull(value) || isUndefined(value)
}

export function isValid<T>(value: T | undefined | null): value is T {
  return !isNil(value)
}

// aliasing this for legacy purposes
export function isDefined<T>(value: T | undefined | null): value is T {
  return isValid(value)
}

export function isObject(value: any): value is object {
  return Object.prototype.toString.call(value) === "[object Object]"
}

export function keysOf<T extends object, K extends keyof T>(obj: T): K[] {
  return Object.keys(obj) as K[]
}

export function valuesOf<T extends object, K extends keyof T>(obj: T): T[K][] {
  return Object.values(obj) as T[K][]
}

export function parseJSON<T>(value: string | null): T | undefined {
  try {
    return isUndefined(value) ? undefined : JSON.parse(value ?? "")
  } catch {
    console.log("parsing error on", { value })
    return undefined
  }
}
