import * as React from "react"
import type { HTMLReport, ReportFilter, ReportStats, ExpandedTests } from "../../types"
import { ReportFrame } from "../ReportFrame"
import { useLocalStorage } from "../../hooks/use-local-storage"
import { ReportContent } from "./ReportContent"

export interface IReportDisplay {
  stats?: ReportStats
  report?: HTMLReport
  isShowing?: boolean
  testsAreRunning?: boolean
  initialized?: boolean
}

export function useReportDisplay() {
  const frameRef = React.useRef<HTMLIFrameElement | null>(null)
  const [isShowing, setIsShowing] = useLocalStorage("microwave-report-is-showing", false)
  const [displayFilter, setDisplayFilter] = React.useState<ReportFilter>("all")
  const [expandedTests, setExpandedTests] = React.useState<ExpandedTests>(new Map())
  // const [hasMounted, setHasMounted] = React.useState(false)

  return {
    frameRef,
    isShowing,
    setIsShowing,
    displayFilter,
    setDisplayFilter,
    expandedTests,
    setExpandedTests,
    // hasMounted,
    // setHasMounted,
  }
}

export function ReportDisplay(props: IReportDisplay) {
  const {
    frameRef,
    isShowing,
    setIsShowing,
    displayFilter,
    setDisplayFilter,
    expandedTests,
    setExpandedTests,
    // handleOnMount,
  } = useReportDisplay()

  return (
    <ReportFrame
      frameRef={frameRef}
      initialized={props.initialized ? 1 : 0}
      passing={props.report?.stats?.passed ?? 0}
      total={props.report?.stats?.total ?? 0}
      isShowing={isShowing}
      setIsShowing={setIsShowing}
      // onUpdate={() => handleOnMount()}
      // onMount={() => handleOnMount()}
    >
      <ReportContent
        report={props.report}
        stats={props.report?.stats}
        displayFilter={displayFilter}
        setDisplayFilter={setDisplayFilter}
        expandedTests={expandedTests}
        setExpandedTests={setExpandedTests}
        setIsShowing={setIsShowing}
        isShowing={isShowing}
        testsAreRunning={props.testsAreRunning}
      />
    </ReportFrame>
  )
}
