import * as React from "react"
import * as ReactDOM from "react-dom"
import { renderToStaticMarkup } from "react-dom/server"
import { isDefined } from "../../utils"
import { Content } from "./Content"
import { createHead } from "./createHead"

import { FrameContextProvider } from "./Context"

export type ReportFrameProps = {
  children: React.ReactChild
  className?: string
  mountTarget?: string
  onMount?: () => void
  onUpdate?: () => void
  isShowing?: boolean
  initialized?: number
  setIsShowing?: (isShowing: boolean) => void
  passing?: number
  total?: number
  frameRef: React.MutableRefObject<HTMLIFrameElement | null>
}
export function ReportFrame(props: ReportFrameProps) {
  const { frameRef } = props

  const setNodeRef = (node: HTMLIFrameElement) => {
    if (isDefined(node) && !isDefined(frameRef.current)) {
      frameRef.current = node

      props.onMount?.()
    }
  }

  const getDoc = () => {
    if (isDefined(frameRef.current)) {
      return frameRef.current.contentDocument
    }
    return null
  }

  const createFrameContents = () => {
    if (!isDefined(frameRef.current)) return null

    const doc = getDoc()

    if (!isDefined(doc)) return null

    const win = doc.defaultView

    const contents = (
      <Content onUpdate={props.onUpdate} onMount={props.onMount}>
        <FrameContextProvider value={{ document: doc, window: win ?? undefined, initialized: props.initialized ?? 0 }}>
          {props?.children ?? null}
        </FrameContextProvider>
      </Content>
    )

    return contents
  }

  const getMountTarget = () => {
    const doc = getDoc()
    let target: HTMLElement | null | undefined

    if (isDefined(props?.mountTarget)) {
      target = doc?.querySelector(props?.mountTarget)
    }

    if (isDefined(doc) && !isDefined(target)) {
      target = doc?.body?.children?.[0] as HTMLElement
    }

    if (isDefined(frameRef?.current) && !isDefined(target)) {
      const frameDoc = frameRef?.current?.contentDocument
      const body = frameRef?.current?.contentDocument?.body
      if (!body?.children?.length) {
        target = frameDoc?.createElement("main")
        if (target) {
          target?.setAttribute("id", "instant-noodles-react-live-test-frame-root")
          body?.appendChild(target)
        }
      }
      target = frameRef?.current?.contentDocument?.body?.children?.[0] as HTMLElement
    }

    return target
  }

  const createIFrameProps = (contents: JSX.Element | null, target?: HTMLElement | null) => {
    const iframeprops = {
      srcDoc: createDefaultSrcWithBody(createHead()),
      className: "sandbox-iframe-element" + isDefined(contents) ? "" : "" + isDefined(target) ? "" : "",
    }

    return iframeprops
  }

  const d = getDoc()
  const contents = createFrameContents()
  const contentsMountTarget = getMountTarget()
  const frameProps = createIFrameProps(contents, contentsMountTarget)
  const iframeStyle = getIframeStyle(props.isShowing)

  return (
    <React.Fragment>
      <iframe
        {...frameProps}
        ref={setNodeRef}
        // used to be ts-ignoring this
        style={iframeStyle}
      >
        {isDefined(d) && isDefined(contentsMountTarget) ? ReactDOM.createPortal(contents, contentsMountTarget) : null}
      </iframe>
    </React.Fragment>
  )
}

type IFrameStyles = React.DetailedHTMLProps<React.IframeHTMLAttributes<HTMLIFrameElement>, HTMLIFrameElement>

const getIframeStyle = (isShowing = false): IFrameStyles => {
  const defaultStyles = {
    right: 0,
    top: 0,
    bottom: 0,
    height: "100%",
    width: "100%",
    position: "fixed",
    display: "flex",
    flexDirection: "column",
    zIndex: 1000000000000,
    border: "none",
  }

  const styles = isShowing
    ? {
        ...defaultStyles,
        height: "100vh",
        width: "800px",
        maxWidth: "100vw",
        transform: "translateX(0)",
        overflowY: "scroll",
      }
    : {
        ...defaultStyles,
        height: 50,
        width: 50,
        alignItems: "center",
        justifyContent: "center",
      }

  return styles as IFrameStyles
}

function StaticButton() {
  return (
    <button
      className="react-live-test-report-toggle"
      style={{
        display: "flex",
        flexDirection: "column",
        height: 45,
        width: 45,
        alignItems: "center",
        justifyContent: "center",
        background: "white",
        border: `solid 1px ${"green"}`,
        borderRadius: "50%",
        position: "absolute",
        right: 5,
        top: 5,
        cursor: "pointer",
        fontSize: 10,
        letterSpacing: "0.1px",
        fontWeight: "bold",
      }}
    >
      <span id="react-live-test-small-font" style={{ display: "flex", justifyContent: "center", fontWeight: "bold" }}>
        save to run
      </span>
    </button>
  )
}

const createDefaultSrcWithBody = (
  head: string = "<head></head>",
  body = `<main id="instant-noodles-react-live-test-frame-root" style="overflow-y: scroll">${renderToStaticMarkup(
    StaticButton()
  )}</main>`
) => `<!DOCTYPE html><html>${head}<body>${body}</body></html>`
