import type { ReactNode, ButtonHTMLAttributes, Ref, HTMLAttributes } from "react"
import type { CommonProps } from "../../types"
import { keysOf } from "../../utils"

export const buttonVariantToClassNamesMapping = {
  standard: {
    button: "bg-white border-black",
    externalBorder: "bg-black",
    content: "text-black",
  },
  blue: {
    button: "bg-blue-300 border-black",
    externalBorder: "bg-black",
    content: "text-white",
  },
  yellow: {
    button: "bg-yellow-300 border-black",
    externalBorder: "bg-black",
    content: "text-black",
  },
  pink: {
    button: "bg-pink-200 border-black",
    externalBorder: "bg-black",
    content: "text-black",
  },
  green: {
    button: "bg-green-300 border-black",
    externalBorder: "bg-black",
    content: "text-black",
  },
  red: {
    button: "bg-red-300 border-black",
    externalBorder: "bg-black",
    content: "text-danger",
  },
  info: {
    button: "bg-blue-200 border-black",
    externalBorder: "bg-black",
    content: "text-neutral-600",
  },
  success: {
    button: "bg-green-200 border-black",
    externalBorder: "bg-black",
    content: "text-success",
  },
  warning: {
    button: "bg-yellow-200 border-black",
    externalBorder: "bg-black",
    content: "text-warning",
  },
  danger: {
    button: "bg-red-200 border-black",
    externalBorder: "bg-black",
    content: "text-danger",
  },
  dark: {
    button: "bg-black border-neutral-500",
    externalBorder: "bg-black",
    content: "text-white",
  },
  ghost: {
    button: "bg-white border-none",
    externalBorder: "transparent",
    content: "text-black",
  },
} as const

export const BUTTON_VARIANT = keysOf(buttonVariantToClassNamesMapping)
export type ButtonVariant = typeof BUTTON_VARIANT[number]

export const buttonSizeToClassNamesMapping = {
  sm: {
    button: "px-3 py-2",
    content: "text-sm",
  },
  md: {
    button: "px-4 py-2",
    content: "text-md",
  },
  lg: {
    button: "px-6 py-3",
    content: "text-lg",
  },
  xl: {
    button: "px-12 py-4",
    content: "text-3xl",
  },
}

export const BUTTON_SIZE = keysOf(buttonSizeToClassNamesMapping)
export type ButtonSize = typeof BUTTON_SIZE[number]

export interface ButtonBaseProps extends CommonProps {
  // disabled or not
  disabled?: boolean
  // button size
  size?: ButtonSize
  // button variant
  variant?: ButtonVariant
  // full width button or not
  block?: boolean
  // circle shaped?
  circle?: boolean
  // no border radius
  square?: boolean
  // whether to just show an outline or not
  // outline?: boolean;
  /* Whether to show a spinner */
  isLoading?: boolean
  /* What HTML element to render */
  asComponent?: "button" | "a"
  /* Icon node to show on left side, if any */
  iconLeft?: ReactNode
  /* Icon node to show on right side, if any */
  iconRight?: ReactNode
  /* Classname to pass to root button element */
  className?: string
  /* The content to render inside of the button */
  children?: ReactNode
}

export interface ButtonProps extends ButtonBaseProps, ButtonHTMLAttributes<HTMLButtonElement> {}

export interface ButtonContentProps extends CommonProps {
  /* variant of the button */
  buttonVariant?: ButtonVariant
  /* size of the button */
  buttonSize?: ButtonSize
  /* Whether to show a spinner */
  isLoading?: boolean
  /* Icon node to show on left side, if any */
  iconLeft?: ReactNode
  /* Icon node to show on right side, if any */
  iconRight?: ReactNode
  /**
   * Object of props passed to the <span/> wrapping the content's text/children only (not icon)
   */
  textProps?: HTMLAttributes<HTMLSpanElement> &
    CommonProps & {
      ref?: Ref<HTMLSpanElement>
      "data-text"?: string
    }
  /* The content to render inside of the button */
  children?: ReactNode
}
