import * as React from "react"
import type { ReportFilter, ReportStats } from "../../types"
import { Button } from "../Button"
import * as icons from "../Icons"

const navbarFilterItems: { label: string; value: ReportFilter; stateAttr: string; icon?: any }[] = [
  { label: "All", value: "all", stateAttr: "total" },
  { label: "Passed", value: "passed", stateAttr: "passed", icon: icons.statusIcon("passed") },
  { label: "Failed", value: "failed", stateAttr: "failed", icon: icons.statusIcon("failed") },
  { label: "Skipped", value: "skipped", stateAttr: "skipped" },
]

export interface IReportHeader {
  stats?: ReportStats
  displayFilter: ReportFilter
  setDisplayFilter: (filter: ReportFilter) => void
}
export function ReportHeader({ stats, setDisplayFilter, displayFilter }: IReportHeader) {
  return (
    <nav className="report-navbar">
      {navbarFilterItems.map((item) => (
        <React.Fragment key={item.value}>
          <Button
            className={`nav-item ${item.value === displayFilter ? "active" : ""}`}
            onClick={() => setDisplayFilter(item.value)}
          >
            {item.icon ?? null} {item.label}{" "}
            <span className="counter">{stats?.[item.stateAttr as keyof typeof stats] ?? 0}</span>
          </Button>
        </React.Fragment>
      ))}
    </nav>
  )
}
