import type { RenderUi } from "./types"
import * as React from "react"
import * as parseUtils from "./utils/parse-utils"
import * as ReactTestRenderer from "react-test-renderer"

import {
  render as rtlRender,
  fireEvent,
  waitFor,
  screen,
  queries,
  within,
  waitForElementToBeRemoved,
  cleanup,
  configure,
  queryHelpers,
  createEvent,
  prettyFormat,
  prettyDOM,
  buildQueries,
  getSuggestedQuery,
  getConfig,
  isInaccessible,
  logDOM,
  logRoles,
  getElementError,
} from "@testing-library/react"
import { assert, exec, type IMicrowaveSuite, suite, updateConfig } from "react-microwave"

const htmlDocumentString = `<!DOCTYPE html>
<html>
  <head>
    <meta charset="utf-8" />
    <title>React Challenges</title>
    <meta name="viewport" content="width=device-width, initial-scale=1" />
  </head>
  <body>
    <main></main>
  </body>
</html>`

const documentCache: { doc: Document | null } = {
  doc: null,
}

const createHtmlDocumentShim = (html = htmlDocumentString) => {
  if (!documentCache.doc) {
    documentCache.doc = new window.DOMParser().parseFromString(html, "text/html")
    if (!documentCache.doc?.defaultView) {
      Object.defineProperty(documentCache.doc, "defaultView", {
        value: window,
      })
    }
  }

  return documentCache.doc
}

const createResetFunction = (dummyDocument: Document) => {
  function reset() {
    dummyDocument.title = ""
    dummyDocument.head.innerHTML = `<meta charset="utf-8" />
  <title>React Challenges</title>
  <meta name="viewport" content="width=device-width,initial-scale=1" />`
    dummyDocument.body.innerHTML = "<main></main>"
  }

  return reset
}

const createRenderFunction = (dummyDocument: Document) => {
  function render(ui: React.ReactElement, options = {}): ReturnType<typeof rtlRender> {
    const Wrapper = ({ children }: { children: RenderUi }) => <React.Fragment>{children}</React.Fragment>
    return rtlRender(ui, {
      container: dummyDocument.querySelector("main") as HTMLElement,
      wrapper: Wrapper,
      ...options,
    })
  }

  return render
}

function configureMicrowave(dummyDocument: Document) {
  const reset = createResetFunction(dummyDocument)

  const wrappedSuite = (name: string): IMicrowaveSuite => {
    const testSuite = suite(name) as IMicrowaveSuite
    // ensure the document is reset after each test
    testSuite.after.each?.(() => {
      reset()
      cleanup()
    })

    // return the original test suite
    return testSuite
  }

  return {
    assert,
    exec,
    suite: wrappedSuite,
    updateConfig,
  }
}

export function configureSpecSuite() {
  const dummyDocument = createHtmlDocumentShim()
  const render = createRenderFunction(dummyDocument)
  const microwave = configureMicrowave(dummyDocument)

  return {
    render,
    fireEvent,
    waitFor,
    screen,
    queries,
    within,
    waitForElementToBeRemoved,
    cleanup,
    configure,
    queryHelpers,
    createEvent,
    prettyFormat,
    prettyDOM,
    buildQueries,
    getSuggestedQuery,
    getConfig,
    isInaccessible,
    logDOM,
    logRoles,
    getElementError,
    reactTestRenderer: {
      create: ReactTestRenderer.create,
      act: ReactTestRenderer.act,
    },
    parseUtils,
    dummyDocument,
    ...microwave,
  }
}
