import * as React from "react"
import type { TestCaseResultsStatus } from "../../types"

export function getValidHtmlStringForErrorMessage(e: string) {
  let lastIndex = -1

  lastIndex = e.indexOf("(Actual)")
  if (lastIndex > -1) {
    return e.slice(0, lastIndex + 8)
  }

  lastIndex = e.indexOf("(ok)")
  if (lastIndex > -1) {
    return e.slice(0, lastIndex + 4)
  }

  lastIndex = e.indexOf("at assert")
  if (lastIndex > -1) {
    return e.slice(0, lastIndex + 9)
  }

  return e
}

export const htmlFormatStatus = (status: TestCaseResultsStatus) => {
  if (status === "passed")
    return <span style={{ fontWeight: "bold", color: "var(--color-scale-green-4)" }}>{status}</span>
  if (status === "failed")
    return <span style={{ fontWeight: "bold", color: "var(--color-scale-red-5)" }}>{status}</span>
  if (status === "skipped")
    return <span style={{ fontWeight: "bold", color: "var(--color-scale-gray-4)" }}>{status}</span>
  return status
}

export const stripExtraneousValuesFromErrorString = (e: string) => e.replace("[90m", "").replace("[39m", "")

export const createHtmlFromError = (e: string) => {
  const eLines = stripExtraneousValuesFromErrorString(e).split("\n")
  const formattedELines = eLines
    .filter((l) => Boolean(l?.trim()) && Boolean(l.trim()))
    // .map((l) => `<p style="display: flex; flex-wrap: wrap; align-items: center;">${l}</p>`)
    .map((l) => `<p style="display: flex">${l}</p>`)
    .join("")

  const htmlToRender = getValidHtmlStringForErrorMessage(formattedELines)
  return htmlToRender
}
