import { type Dispatch, type SetStateAction, useCallback, useEffect, useRef, useState } from "react"
import { useEventListener } from "./use-event-listener"
import { parseJSON } from "../utils"

type SetValue<T> = Dispatch<SetStateAction<T>>

export function useLocalStorage<T>(key: string, initialValue: T): [T, SetValue<T>] {
  const readValue = useCallback((): T => {
    if (typeof window === "undefined") {
      return initialValue
    }

    try {
      const item = window.localStorage.getItem(key)
      return item ? (parseJSON(item) as T) : initialValue
    } catch (error) {
      console.warn(`Error reading localStorage key â€œ${key}â€:`, error)
      return initialValue
    }
  }, [initialValue, key])

  const [storedValue, setStoredValue] = useState<T>(readValue())

  const setValueRef = useRef<SetValue<T>>()

  setValueRef.current = (value) => {
    if (typeof window == "undefined") {
      console.warn(`Tried setting localStorage key â€œ${key}â€ even though environment is not a client`)
    }

    try {
      const newValue = value instanceof Function ? value(storedValue) : value
      window.localStorage.setItem(key, JSON.stringify(newValue))
      setStoredValue(newValue)
    } catch (error) {
      console.warn(`Error setting localStorage key â€œ${key}â€:`, error)
    }
  }

  const setValue: SetValue<T> = useCallback((value) => setValueRef.current?.(value), [])

  useEffect(() => {
    setStoredValue(readValue())
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const handleStorageChange = useCallback(() => {
    setStoredValue(readValue())
  }, [readValue])

  useEventListener("storage", handleStorageChange)

  return [storedValue, setValue]
}
