const getIcon = (status: TestCaseResultsStatus) => {
  switch (status) {
    case "failed":
      return cross()
    case "passed":
      return check()
    case "timedOut":
      return warning()
    case "skipped":
      return blank()
  }
}

export function statusIcon(status: TestCaseResultsStatus): JSX.Element {
  return (
    <React.Fragment>
      <>{getIcon(status)}</>
    </React.Fragment>
  )
}
