import * as React from "react"
import * as icons from "../Icons"
import type { CommonProps } from "../../types"

export interface IExpandableCommon extends CommonProps {
  header: JSX.Element | string
  noInsets?: boolean
}

export interface IExpandable extends IExpandableCommon {
  expanded?: boolean
  setExpanded?: (expanded: boolean) => void
}

export const Expandable: React.FC<IExpandable> = ({ header, expanded, setExpanded, children, noInsets }) => {
  return (
    <div className="expandable">
      <div
        className={`expandable-header ${setExpanded ? `expanded-${expanded}` : ""}`}
        onClick={() => setExpanded?.(!expanded)}
        title={typeof header === "string" ? header : undefined}
      >
        {setExpanded && !!expanded && icons.downArrow()}
        {setExpanded && !expanded && icons.rightArrow()}
        {header}
      </div>
      {(!setExpanded || expanded) && (
        <div className={"expandable-body" + (noInsets ? " expandable-body-no-insets" : "")}>{children}</div>
      )}
    </div>
  )
}

export interface IAutoExpandable extends IExpandableCommon {
  initialExpanded?: boolean
}
export const AutoExpandable: React.FC<IAutoExpandable> = ({ header, initialExpanded, noInsets, children }) => {
  const [expanded, setExpanded] = React.useState(initialExpanded || initialExpanded === undefined)
  return (
    <Expandable header={header} expanded={expanded} setExpanded={setExpanded} noInsets={noInsets}>
      <React.Fragment>{children}</React.Fragment>
    </Expandable>
  )
}
