import * as React from "react"
import type { HTMLReport, ITestSuiteSummary, ReportFilter, ExpandedTests } from "../../types"
import { Loader } from "../Loader/Loader"
import { TestSuite } from "./Suite"

interface ISuiteResults {
  report?: HTMLReport
  expandedTests: ExpandedTests
  setExpandedTests: (value: ExpandedTests) => void
  defaultExpanded?: boolean
  displayFilter?: ReportFilter
  testsAreRunning?: boolean
}
export const SuiteResults = ({
  report,
  expandedTests,
  setExpandedTests,
  displayFilter,
  defaultExpanded = true,
  testsAreRunning,
}: ISuiteResults) => {
  const filteredSuites = React.useMemo(() => {
    const result: { suite: ITestSuiteSummary; defaultExpanded: boolean }[] = []
    let visibleTests = 0
    for (const suite of report?.suites || []) {
      const tests = suite.tests
      visibleTests += tests.length
      if (tests.length) result.push({ suite, defaultExpanded: visibleTests < 200 })
    }
    return result
  }, [report])

  if (testsAreRunning) {
    return (
      <div className="suites">
        <Loader />
      </div>
    )
  }

  return (
    <div className="suites">
      {report?.suites
        ? filteredSuites.map(({ suite }) => {
            return (
              <TestSuite
                key={`suite-${suite.suiteName}`}
                displayFilter={displayFilter}
                report={report}
                suite={suite}
                isTestExpanded={(fileId) => {
                  const value = expandedTests.get(fileId)
                  if (value === undefined) return defaultExpanded
                  return !!value
                }}
                setExpandedTests={(fileId, expanded) => {
                  const newExpanded = new Map(expandedTests)
                  newExpanded.set(fileId, expanded)
                  setExpandedTests(newExpanded)
                }}
              />
            )
          })
        : null}
    </div>
  )
}
