import type { TestSuiteMapping, ReportResults } from "../../types"
import type { MicrowaveConfig } from "react-microwave"

import * as React from "react"
import * as ReactDOM from "react-dom"
import { ReportDisplay } from "../ReportDisplay"
import { useExecuteTestSuites, UseExecuteTestSuitesContext } from "../../hooks/use-execute-test-suites"
import { isDefined } from "../../utils"
import * as constants from "../../constants"

export interface InstantNoodlesProps {
  RootComponent?: React.ComponentType<any>
  tests?: TestSuiteMapping
  config?: Partial<MicrowaveConfig>
}

export interface UseInstantNoodlesContext extends UseExecuteTestSuitesContext {
  container: React.RefObject<HTMLElement>
  initialized: boolean
  reportExists: boolean
}

export function useInstantNoodles({ tests, RootComponent, config }: InstantNoodlesProps): UseInstantNoodlesContext {
  const container = React.useRef<HTMLElement | null>(null)
  const [initialized, setInitialized] = React.useState(false)
  const { report, testsAreRunning } = useExecuteTestSuites({
    testSuites: tests,
    RootComponent,
    config,
  })

  const reportExists = isDefined(report?.stats)

  React.useEffect(() => {
    if (!container.current && !initialized) {
      // const existingEl = window.document.getElementById("microwave-report-div-root")
      const existingEl = window.document.getElementById(constants.MICROWAVE_LIVE_REPORT_ROOT_DIV_ID)
      if (existingEl) {
        container.current = existingEl
      } else {
        const el = window.document.createElement("div")
        // el.setAttribute("id", "microwave-report-div-root")
        el.setAttribute("id", constants.MICROWAVE_LIVE_REPORT_ROOT_DIV_ID)
        container.current = el
        window.document.body.appendChild(el)
      }

      setInitialized(true)
    }
  }, [initialized])

  return { report, testsAreRunning, container, initialized, reportExists }
}

export function InstantNoodles(props: InstantNoodlesProps) {
  const { report, testsAreRunning, container, initialized, reportExists } = useInstantNoodles({
    tests: props.tests,
    RootComponent: props.RootComponent,
    config: props.config,
  })

  return (
    <React.Fragment>
      {container.current
        ? ReactDOM.createPortal(
            <ReportDisplay
              report={report as ReportResults}
              testsAreRunning={testsAreRunning}
              initialized={initialized}
            />,
            container.current!
          )
        : null}
    </React.Fragment>
  )
}
