import * as React from "react"

export interface IToggleReportButtonProps {
  numPassed?: number
  numTotal?: number
  setIsShowing?: (isShowing: boolean) => void
  style?: React.CSSProperties
}

export function ToggleReportButton({ setIsShowing, numPassed, numTotal, ...props }: IToggleReportButtonProps) {
  const borderColor = numPassed === numTotal ? "green" : "red"

  return (
    <button
      className="react-live-test-report-toggle"
      style={{
        display: "flex",
        flexDirection: "column",
        height: 45,
        width: 45,
        alignItems: "center",
        justifyContent: "center",
        background: "white",
        border: `solid 1px ${borderColor}`,
        borderRadius: "50%",
        position: "absolute",
        right: 5,
        top: 5,
        cursor: "pointer",
        fontSize: 12,
        // letterSpacing: "4px",
        fontWeight: "bold",
      }}
      onClick={() => setIsShowing?.(true)}
      {...props}
    >
      <span style={{ display: "flex", justifyContent: "center", fontWeight: "bold" }}>
        {numPassed ?? 0}/{numTotal ?? 0}
      </span>
    </button>
  )
}
