import * as React from "react"
import { buttonSizeToClassNamesMapping, buttonVariantToClassNamesMapping, ButtonProps } from "./ButtonTypes"
import ButtonContent from "./ButtonContent"
import { classNames, noop } from "../../utils"

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  (
    {
      disabled = false,
      variant = "standard",
      size = "md",
      circle = false,
      square = false,
      block = false,
      isLoading = false,
      asComponent = "button",
      onClick = noop,
      iconLeft = null,
      iconRight = null,
      className = "",
      children,
      ...props
    },
    ref
  ) => {
    const variantClasses = buttonVariantToClassNamesMapping[variant]
    const sizeClasses = buttonSizeToClassNamesMapping[size]

    const handleOnClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
      if (disabled) return
      if (isLoading) return
      onClick(e)
    }

    const borderRadiusClassname = circle ? "rounded-full" : square ? "" : "rounded-xl"

    const buttonClassNames = classNames([
      "instant-noodles-ui-element instant-noodles-ui-button",
      "relative",
      variant === "ghost" ? "" : "border-2",
      block ? "w-full" : "",
      asComponent === "button" ? "" : "inline-block",
      borderRadiusClassname,
      // interaction classes
      variant === "ghost" ? "" : "shadow-full",
      "challenge-ui-animated-button",
      "duration-200",
      // 'active:translate-y-px',
      variant === "ghost" || disabled || isLoading ? "" : "active:translate-y-px active:shadow-fourth",
      disabled ? "cursor-not-allowed" : isLoading ? "cursor-wait" : "cursor-pointer",
      // size classes
      sizeClasses.button,
      // variants classes
      variantClasses.button,
      // customs
      className,
    ])
    const buttonContentClassNames = classNames([
      // position content
      "flex flex-row items-center",
      // wrap or not
      "whitespace-nowrap",
      // text styling
      "font-extrabold",
      // size classes
      sizeClasses.content,
      // variant classes
      variantClasses.content,
    ])

    return React.createElement(
      asComponent,
      {
        className: buttonClassNames,
        onClick: handleOnClick,
        disabled,
        ref,
        ...props,
      },
      <ButtonContent
        buttonVariant={variant}
        buttonSize={size}
        isLoading={isLoading}
        iconLeft={iconLeft}
        iconRight={iconRight}
        className={buttonContentClassNames}
      >
        <React.Fragment>{children}</React.Fragment>
      </ButtonContent>
    )
  }
)

Button.displayName = "Button"

export default Button
