import type { HTMLReport, ITestSuiteSummary, ITestCaseSummary, ReportFilter, ExpandedTests } from "../../types"
import * as React from "react"
import { Expandable } from "../Expandable/Expandable"
import * as icons from "../Icons"
import { msToString } from "../../utils"
import * as helpers from "./helpers"

export interface ITestSuite {
  report: HTMLReport
  suite: ITestSuiteSummary
  isTestExpanded: (testId: string) => boolean
  setExpandedTests: (fileId: string, expanded: boolean) => void
  displayFilter?: ReportFilter
}
export const TestSuite = ({ suite, isTestExpanded, setExpandedTests, displayFilter }: ITestSuite) => {
  const [expandedSuiteTests, setExpandedSuiteTests] = React.useState<ExpandedTests>(new Map())

  const filteredSuiteTests = React.useMemo(() => {
    if (!displayFilter || displayFilter === "all") return suite.tests
    return suite.tests.filter((test) => test.status === displayFilter)
    // TODO: Figure out if we want to update the deps for this section
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [displayFilter])

  return (
    <Expandable
      expanded={isTestExpanded(suite.suiteName)}
      noInsets={true}
      setExpanded={(expanded) => setExpandedTests(suite.suiteName, expanded)}
      header={
        <span>
          <span style={{ float: "right", marginLeft: 5 }}>{msToString(suite.stats.duration)}</span>
          {suite.suiteName}
        </span>
      }
    >
      {filteredSuiteTests.length === 0 ? (
        <div className={"test-case"}>
          <span className="test-case-desc">No tests match that filter for this suite</span>
        </div>
      ) : null}
      {filteredSuiteTests.map((test: ITestCaseSummary) => (
        <React.Fragment key={`test-${test.order}`}>
          <div
            className={"test-case test-case-outcome-" + test.status}
            role="button"
            onClick={() => {
              const isExpanded = expandedSuiteTests.get(test.order)
              const newExpanded = new Map(expandedSuiteTests)
              newExpanded.set(test.order, !isExpanded)
              setExpandedSuiteTests(newExpanded)
            }}
          >
            <span style={{ float: "right" }}>{msToString(test.duration)}</span>
            {icons.statusIcon(test.status)}
            <span className="test-case-desc">â€” {test.description}</span>
          </div>
          {expandedSuiteTests.get(test.order) ? (
            <>
              {test.errors?.length ? (
                <div className="error-message-box-wrapper">
                  {test.errors.map((e, i) => {
                    const htmlToRender = helpers.createHtmlFromError(e)
                    return (
                      <div
                        key={`error-${i}`}
                        className="error-message-box"
                        dangerouslySetInnerHTML={{ __html: htmlToRender }}
                        // onClick={() => console.log({ e })}
                      />
                    )
                  })}
                </div>
              ) : (
                <div className="success-message-box-wrapper">
                  <div>{test.description}</div>
                  <div>Status: {helpers.htmlFormatStatus(test.status)}</div>
                </div>
              )}
            </>
          ) : null}
        </React.Fragment>
      ))}
    </Expandable>
  )
}
