function rootStyles() {
  return `
<style>

:root {
  --color-canvas-subtle: #161b22;
  --color-border-default: #30363d;

  --color-fg-default: #c9d1d9;
  --color-fg-muted: #8b949e;
  --color-fg-subtle: #484f58;  

  --color-checks-step-header-open-bg: #161b22;
  --color-checks-step-error-text: #f85149;
  --color-checks-step-warning-text: #d29922;  

  --color-neutral-emphasis-plus: #6e7681;
  --color-neutral-emphasis: #6e7681;
  --color-neutral-muted: rgba(110,118,129,0.4);
  --color-neutral-subtle: rgba(110,118,129,0.1);

  --color-accent-fg: #58a6ff;
  --color-accent-emphasis: #1f6feb;
  --color-accent-muted: rgba(56,139,253,0.4);
  --color-accent-subtle: rgba(56,139,253,0.15);

  --color-success-fg: #3fb950;
  --color-success-emphasis: #238636;
  --color-success-muted: rgba(46,160,67,0.4);
  --color-success-subtle: rgba(46,160,67,0.15);

  --color-attention-fg: #d29922;
  --color-attention-emphasis: #9e6a03;
  --color-attention-muted: rgba(187,128,9,0.4);
  --color-attention-subtle: rgba(187,128,9,0.15);

  --color-severe-fg: #db6d28;
  --color-severe-emphasis: #bd561d;
  --color-severe-muted: rgba(219,109,40,0.4);
  --color-severe-subtle: rgba(219,109,40,0.15);

  --color-danger-fg: #f85149;
  --color-danger-emphasis: #da3633;
  --color-danger-muted: rgba(248,81,73,0.4);
  --color-danger-subtle: rgba(248,81,73,0.15);

  --color-done-fg: #a371f7;
  --color-done-emphasis: #8957e5;
  --color-done-muted: rgba(163,113,247,0.4);
  --color-done-subtle: rgba(163,113,247,0.15);  

  --color-scale-black: #010409;
  --color-scale-white: #f0f6fc;

  --color-scale-gray-0: #f0f6fc;
  --color-scale-gray-1: #c9d1d9;
  --color-scale-gray-2: #b1bac4;
  --color-scale-gray-3: #8b949e;
  --color-scale-gray-4: #6e7681;
  --color-scale-gray-5: #484f58;
  --color-scale-gray-6: #30363d;
  --color-scale-gray-7: #21262d;
  --color-scale-gray-8: #161b22;
  --color-scale-gray-9: #0d1117;  
}
</style>  
  `
}

function reportGlobalStyles() {
  return `
<style>
* {
  box-sizing: border-box;
  min-width: 0;
  min-height: 0;
  font-family: "SF Mono", Monaco, Consolas, "Droid Sans Mono", Inconsolata, "Courier New", monospace;
  font-size: 13px;
}

html, body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  overscroll-behavior-x: none;
}

body {
  overflow: auto;
  max-width: 1024px;
  margin: 0 auto;
  width: 100%;
}

#react-live-test-small-font {
  font-size: 10px;
}

svg {
  fill: currentColor;
}

.mr-1 { margin-right: 4px; }
.mr-2 { margin-right: 8px; }
.mr-3 { margin-right: 16px; }
.mr-4 { margin-right: 24px; }
.mr-5 { margin-right: 32px; }

.ml-1 { margin-left: 4px; }
.ml-2 { margin-left: 8px; }
.ml-3 { margin-left: 16px; }
.ml-4 { margin-left: 24px; }
.ml-5 { margin-left: 32px; }

.p-1 { padding: 4px; }
.p-2 { padding: 8px; }
.p-3 { padding: 16px; }
.p-4 { padding: 24px; }
.p-5 { padding: 32px; }

.px-1 { padding: 0 4px; }
.px-2 { padding: 0 8px; }
.px-3 { padding: 0 16px; }
.px-4 { padding: 0 24px; }
.px-5 { padding: 0 32px; }

.py-1 { padding: 4px 0; }
.py-2 { padding: 8px 0; }
.py-3 { padding: 16px 0; }
.py-4 { padding: 24px 0; }
.py-5 { padding: 32px 0; }

.float-left {
  float: left !important;
}

article, aside, details, figcaption, figure, footer, header, main, menu, nav, section {
  display: block;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
span,
p,
div {
  margin: 0;
  padding: 0;
}  

</style>
  `
}

function iconStyles() {
  return `
<style>
.color-icon-success {
  color: var(--color-success-fg) !important;
}

.color-text-danger {
  color: var(--color-danger-fg) !important;
}

.color-text-warning {
  color: var(--color-checks-step-warning-text) !important;
}

.color-fg-muted {
  color: var(--color-fg-muted) !important;
}

.octicon {
  display: inline-block;
  overflow: visible !important;
  vertical-align: text-bottom;
  fill: currentColor;
  margin-right: 7px;
  flex: none;
}
</style>  
  `
}

function reportNavbarStyles() {
  return `
<style>

.report-navbar {
  display: flex;
  whitespace: nowrap;
}

.nav-item {
  flex: none;
  position: relative;
  float: left;
  padding: 5px 10px;
  font-weight: 500;
  line-height: 20px;
  color: var(--color-fg-default);
  color: var(--color-fg-subtle);
  color: var(--color-fg-muted);
  border: 1px solid var(--color-border-default);
  cursor: pointer;
  background-color: rgb(249, 249, 249);
}

.nav-item:hover, 
.nav-item.active {
  background-color: var(--color-canvas-subtle);
  color: white;
}

.nav-item:first-child {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}

.nav-item:last-child {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}

.nav-item + .subnav-item {
  margin-left: -1px;
}

.counter {
  display: inline-block;
  min-width: 20px;
  padding: 0 6px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  color: var(--color-fg-default);
  color: white;
  text-align: center;
  background-color: var(--color-neutral-muted);
  border: 1px solid transparent;
  border-radius: 2em;
}

.nav-item:hover .counter, 
.nav-item.active .counter {
  color: var(--color-fg-default);
  background-color: var(--color-neutral-muted);  
}


@media only screen and (max-width: 600px) {
  .nav-item {
    border-radius: 0 !important;
  }

  .nav-item {
    padding: 5px 3px;
    border: none;
  }
}  

</style>
  `
}

function expandableStyles() {
  return `

<style>
.expandable { 
  background: white;
}

.expandable-header {
  border: 1px solid var(--color-border-default);
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background-color: var(--color-canvas-subtle);
  padding: 0 8px;
  border-bottom: none;
  margin-top: 24px;
  font-weight: 600;
  line-height: 38px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
}

.expandable-header.expanded-false {
  border: 1px solid var(--color-border-default);
  border-radius: 6px;
}

.expandable-header.expanded-false,
.expandable-header.expanded-true {
  cursor: pointer;
}

.expandable-body {
  border: 1px solid var(--color-border-default);
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  padding: 16px;
}

.expandable-body-no-insets {
  padding: 0;
}

@media only screen and (max-width: 600px) {
  .expandable-header {
    border-radius: 0;
    border-right: none;
    border-left: none;
  }

  .expandable-body {
    border-radius: 0;
    border-right: none;
    border-left: none;
    padding: 8px;
  }

  .expandable-body-no-insets {
    padding: 0; 
  }
}

</style>    
  `
}

