function testResultStyles() {
  return `

<style>

.test-case {
  height: 38px;
  line-height: 38px;
  align-items: center;
  padding: 0 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}

.test-case:hover {
  background-color: var(--color-canvas-subtle);
  color: white;
}

.test-case-desc {
  padding: 0 0 0 8px;
  color: var(--color-fg-muted);
}

.test-case-outcome-skipped {
  color: var(--color-fg-muted);
}

.test-case:not(:first-child) {
  border-top: 1px solid var(--color-border-default);
}

</style>  
  `
}

function errorMessageBoxStyles() {
  return `
<style>
.success-message-box-wrapper {
  border-top: solid 1px rgba(222, 222, 222, 0.9);
  padding: 8px;  
}

.error-message-box-wrapper {
  border-top: solid 1px rgba(222, 222, 222, 0.9);
  padding: 4px 0;
}
.error-message-box {
  padding: 4px 8px;
  display: flex; 
  flex-direction: column;
  gap: 0.75rem;
  max-width: 100%;
}
.error-message-box .title {
  font-weight: bold;
  text-transform: uppercase;
}  
.error-message-box .italic {
  font-style: italic;
}    
.error-message-box .underline {
  text-decoration: underline;
}      
.error-message-box .dim {
  color: var(--color-scale-gray-2);
}
.error-message-box .error-text.test-name {
  text-decoration: red wavy underline;
}
.error-message-box .error-text.suite-name {
  font-weight: bold;
}
</style>  
  `
}

function reportStyles() {
  return `

<style>
.htmlreport {
  position: relative;
  background: white;
  background: rgb(248, 249, 253);
  height: 100%;
  height: 100vh;
  overflow-y: scroll;
  border-left: solid 1px #cecece;
  padding-bottom: 1rem;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  flex: auto;
}
.htmlreport .report-close-icon {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}
span.html-colored-formatter {
  /* word-wrap: break-word; */
  overflow-wrap: break-word;
}
span.pretty {
  word-break: break-word;
}
.suites {
  padding-bottom: 1rem;
}
@media only screen and (max-width: 600px) {
  .report {
    padding: 0 !important;
  }
}  
</style>
  `
}

function loaderStyles() {
  return `

<style>
.regular-loading .loader {
  margin: auto;
  width: 60px;
  height: 50px;
  font-size: 10px;
  top: calc(50% - 25px);
  position: relative;
}

.regular-loading .loader>span {
  height: 100%;
  width: 5px;
  display: block;
  float: left;
  margin: 0 7.5px;
  margin-bottom: 0.6em;
  animation: loader-animation-red 1.2s infinite ease-in-out;
}

.regular-loading .loader span:nth-child(2) {
  animation-delay: -1s;
}

.regular-loading .loader span:nth-child(3) {
  animation-delay: -0.8s;
}

@keyframes loader-animation-red {

  0%,
  20% {
      background-color: #999999;
      transform: scaleY(0.6);
      -webkit-transform: scaleY(0.6);
  }

  50% {
      background-color: #cc0000;
      transform: scaleY(1.5);
      -webkit-transform: scaleY(1.5);
  }

  100% {
      background-color: #999999;
      transform: scaleY(0.6);
      -webkit-transform: scaleY(0.6);
  }
}
</style>    
  `
}

export function createHead() {
  return `
<head>

${rootStyles()}

${reportGlobalStyles()}

${iconStyles()}

${reportNavbarStyles()}

${expandableStyles()}

${testResultStyles()}

${errorMessageBoxStyles()}

${reportStyles()}

${loaderStyles()}

</head>
  `
}
